import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'productList',
      component: (resolve) => require(['@/views/product/productList.vue'], resolve)
    },
    {
      path: '/productDetail',
      name: 'productDetail',
      component: (resolve) => require(['@/views/product/productDetail.vue'], resolve)
    }
  ]
})



